.mobile {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: $bgColorMenu;
  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 5;
  overflow-y: auto;

  @media (min-width: 768px) {
    padding: 0 67px;
  }

  @media (min-width: 1280px) {
    display: none;
  }

  &.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}

.menu {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 20px;

  &__logo {
    margin-bottom: 40px;
    width: 190px;
    height: 85px;
  }

  &__close {
    position: absolute;
    top: -2px;
    right: 0px;
    background-color: inherit;
    border: 0 transparent;

    &--x {
      width: 18px;
      height: 18px;
    }
  }
}

.nav {

  @media (min-width: 1280px) {
    position: absolute;
    left: 339px;
    display: inline-block;
    padding-top: 20px;
  }

  &__list {
    font-family: $fontFamilyPrimary;
    font-size: 24px;
    line-height: 1.22;
    font-weight: 500;

    @media (min-width: 1280px) {
      display: flex;
      color: $fontColorPrimary;
      font-size: 18px;
    }

    &--header {
      @media (max-width: 1279.95px) {
        display: none;
      }
    }
  }

  &__item {
    margin-bottom: 45px;

    @media (min-width: 1280px) {
      margin-bottom: 0;
      margin-right: 20px;
    }

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    color: $fontColorPrimary;

    &:hover {
      color: $fontColorAccent;
    }
  }
}